      <dv-border-box-8>
        <el-drawer
          :title="drawerTitle"
          :visible.sync="drawer"
          :direction="direction"
        >
          <data-boxsix :berthInfo="berthInfo"></data-boxsix> </el-drawer
      ></dv-border-box-8>
    </div>
  </div>
</template>

<script>
import DataBoxone from '../berth/DataBoxOne.vue';
import DataBoxtwo from '../berth/DataBoxTwo.vue';
import DataBoxthree from '../berth/DataBoxThree.vue';
import DataBoxfour from '../berth/DataBoxFour.vue';
import DataBoxfive from '../berth/DataBoxFive.vue';
import DataBoxsix from '../berth/DataBoxSix.vue';

import BerthData from '../common/BerthData';

export default {
  components: {
    DataBoxone,
    DataBoxtwo,
    DataBoxthree,
    DataBoxfour,
    DataBoxfive,
    DataBoxsix,
  },
  data() {
    return {
      drawerTitle: ` 600200400  5`,
      // 
      drawer: false,
      direction: 'btt',
      stationInfo: {},
      berthInfo: [], //
    };
  },
  mounted() {
    this.initGaode();
  },
  methods: {
    // 
    initGaode() {
      // console.log(this.position);
      // eslint-disable-next-line no-undef
      let map = new AMap.Map('gaodechart', {
        center: window.center,
        resizeEnable: true,
        rotateEnable: true,
        pitchEnable: true,
        zoom: 16, //
        viewMode: '3D', //3D,
        buildingAnimation: true, //
        pitch: 60,
        rotation: 0,
        expandZoomRange: true,
        zooms: [3, 20],
        showLabel: true,
        showIndoorMap: true,
        mapStyle: 'amap://styles/3ae644b7aeec52c4c4e16f400f07bdf0', //
      });
      let markers = [];
      // 
      BerthData.getRoadMarker(map, markers, (station) => {
        console.log('', station);
        this.stationInfo = station;
        /* this.drawerTitle = `${this.stationInfo.name} ${this.stationInfo.stat.total}${this.stationInfo.stat.idle}${this.stationInfo.stat.busy}  ${this.stationInfo.lot_rate.memo}` */
        this.drawer = true;
        // // 
        // this.roadDetails = stations[i]
        // // 
        // const roadDetails = BerthData.getRoadDetails(station.id);
        const roadDetails = BerthData.getRoadDetails();
        this.berthInfo = roadDetails;
        console.log('', roadDetails);
        // this.roadDetailsData = roadDetails
        // console.log("", stations[i])
      });
    },
  },
};
</script>

