<template>
  <dv-border-box-8 :reverse="true"
    ><div class="mydata2-box">
      <div class="data-title">
        <div class="box-left">
          <span style="color: #4ea4f3"></span>
          <span>7</span>
        </div>
        <div class="box-right">
          <span></span>
          <span style="color: #4ea4f3"></span>
        </div>
      </div>
      <div class="data-box" id="mydata2" ref="trend_ref"></div></div
  ></dv-border-box-8>
</template>

<script>
import ChartOptiion from '../common/ChartOption'

export default {
  data() {
    return {
      myChart: null, // ECharts
    };
  },
  mounted() {
    this.initCharts();
    // window.addEventListener('resize', this.screenAdapter);
    // this.screenAdapter();
    // this.getChartData();
    window.addEventListener('resize', this.screenAdapter);
    this.screenAdapter();
  },
  destroyed() {
    window.removeEventListener('resize', this.screenAdapter);
  },
  // destroyed() {
  //   window.removeEventListener('resize', this.screenAdapter);
  // },
  methods: {
    initCharts() {
      const option = ChartOptiion.weekUsingRate(true, ['1', '2', '3'], [1, 2, 3]);
      this.myChart = this.$echarts.init(document.getElementById('mydata2'));
      this.myChart.setOption(option);
    },
    screenAdapter(){
      this.myChart.resize();
    }
  },
};
</script>

<style scoped lang='less'>
.mydata2-box {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  .data-title {
    width: 100%;
    height: 10%;
    display: flex;
    padding: 0.6rem 1rem 0 1rem;
    box-sizing: border-box;
    color: #fff;
    font-size: 1.4rem;
    justify-content: space-between;
    .box-right {
      span {
        &:last-child {
          margin-left: 1rem;
        }
      }
    }
  }
  .data-box {
    flex: 1;
  }
}
</style>