<template>
  <dv-border-box-8
    ><div class="mydata4-box">
      <div class="data-title">
        <div class="box-left">
          <span style="color: #4ea4f3"></span>
          <span></span>
        </div>
        <div class="box-right">
          <span></span>
          <span style="color: #4ea4f3"></span>
        </div>
      </div>
      <div class="data-box" id="mydata4"></div></div
  ></dv-border-box-8>
</template>

<script>
import ChartOptiion from '../common/ChartOption';

export default {
  data() {
    return {};
  },
  mounted() {
    this.initCharts();
    window.addEventListener('resize', this.screenAdapter);
    this.screenAdapter();
  },
  destroyed() {
    window.removeEventListener('resize', this.screenAdapter);
  },
  methods: {
    initCharts() {
      const option = ChartOptiion.berthTakeRate(true, [
        { value: 1048, name: '' },
        { value: 735, name: '' },
        { value: 580, name: '' },
        { value: 484, name: '' },
        { value: 300, name: '' },
      ]);
      this.myChart = this.$echarts.init(document.getElementById('mydata4'));
      this.myChart.setOption(option);
    },
    screenAdapter() {
      this.myChart.resize();
    },
  },
};
</script>

<style scoped lang='less'>
.mydata4-box {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  .data-title {
    width: 100%;
    height: 15%;
    display: flex;
    padding: 0.6rem 1rem 0 1rem;
    box-sizing: border-box;
    color: #fff;
    font-size: 1.4rem;
    justify-content: space-between;
    .box-right {
      span {
        &:last-child {
          margin-left: 1rem;
        }
      }
    }
  }
  .data-box {
    flex: 1;
  }
}
</style>